body {
  margin: 0;
  padding: 15px !important;
  font-family: sans-serif;
  background: #bfffdca3;
  color: #666;
}
.installDetail {
  max-width: 1100px;
  margin: 0 auto;
  display: block;
}
.installDetail h3 {
  margin-bottom: 0;
}
.installDetail pre {
  font-family: monospace;
  border: 1px solid #e8e8e8;
  padding: 10px;
  background: #fff;
  margin-top: 0;
  -webkit-box-shadow: 1px 1px 2px #eee;
  box-shadow: 1px 1px 2px #eee;
  border-radius: 5px;
  width: 100%;
  overflow-x: auto;
}

.responsive-table {
  width: 100%;
  overflow-x: auto;
  display: block;
}
.installDetail .table {
  width: 100%;
  border: 1px solid #999;
  border-spacing: 0;
  background: #fff;
}
.installDetail .table tr td,
.installDetail .table tr th {
  border-bottom: 1px solid #999;
  color: #666;
  border-left: 1px solid #999;
  white-space: normal;
  padding: 5px;
}
.installDetail .table tr td:first-child {
  width: 120px;
}

.flex {
  display: -ms-flexbox;
  display: flex;
}
.flex > div {
  width: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding-right: 50px;
}



